
import { defineComponent } from 'vue'
import StopWatch from './components/StopWatch.vue'
import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonBackButton,
    IonTitle,
    IonContent,
    IonText,
    IonAccordionGroup,
    IonAccordion,
    IonItem,
    IonLabel,
    IonList,
    IonThumbnail,
    IonImg,
    IonCheckbox,
    IonButton,
} from '@ionic/vue'

import { useDataStore } from '@/stores/data'
import { useRoute } from 'vue-router'

export default defineComponent({
    name: 'ArmWorkoutPage',
    components: {
        IonPage,
        IonHeader,
        IonToolbar,
        IonButtons,
        IonBackButton,
        IonTitle,
        IonContent,
        IonText,
        IonAccordionGroup,
        IonAccordion,
        IonItem,
        IonLabel,
        IonList,
        IonThumbnail,
        IonImg,
        IonCheckbox,
        IonButton,
        StopWatch,
    },
    setup() {
        const data: any = useDataStore()
        const route = useRoute()

        let path = route.params.id

        let workoutPath

        //set workout path from route params id
        switch (path) {
            case 'arm':
                workoutPath = data.workout.workout.arm
                break
            case 'body':
                workoutPath = data.workout.workout.body
                break
            case 'core':
                workoutPath = data.workout.workout.core
                break

            default:
                workoutPath = 'NotFound'
                break
        }

        return {
            data,
            workoutPath,
            path,
        }
    },
    data() {
        return {}
    },
    methods: {
        goTo(link: string) {
            window.open(link)
        },
    },
})
